export default {
	name:'xlist-picker',
	props:{
		selectedItem:String,
		title:{
			type:String,
			default:'企业类型'
		},
		mode:{
			type:String,
			default:'selector'
		},
		range:{
			type:Array
		}
		
	},
	methods: {
		bindChange(e){
			let s = e.detail.value
			this.$emit('selectedIndex', s)
		}
	}
}
